<template>
  <v-app>
    <Layout v-if="Object.keys(currentUser).length !== 0" :title="title" />
    <v-main>
      <v-row class="fill-height">
        <v-col>
          <transition name="fade">
            <router-view></router-view>
          </transition>
        </v-col>
      </v-row>
    </v-main>
    <Snackbar />
    <Loader />
    <ProgressDialog />
  </v-app>
</template>
<script>
import Vue from "vue";
import Snackbar from "@/components/Snackbar";
import Layout from "@/components/Layout";
import Loader from "@/components/Loaders/Loader";
import ProgressDialog from "@/components/Loaders/ProgressDialog";
import firebase from "firebase";
import { mapState } from "vuex";

export default Vue.extend({
  components: {
    Snackbar,
    Layout,
    Loader,
    ProgressDialog,
  },
  data: () => ({
    title: null,
  }),
  computed: {
    ...mapState(
      { currentUser: (state) => state.user.currentUser },
      { isLoggedIn: (state) => state.user.isLoggedIn }
    ),
  },
  updated() {
    const location = window.location.href;
    this.title = location.split("/")[3].charAt(0).toUpperCase() + location.split("/")[3].substring(1)

    this.$store.commit("setLoader", { isLoading: true });
    firebase.auth().onAuthStateChanged(async (user) => {
      if (user) {
        console.log("logged in", user.uid);

        await firebase
          .firestore()
          .collection("users")
          .doc(user.uid)
          .onSnapshot((snapshot) => {
            const userData = snapshot.data();
            const userDetails = {
              uid: user.uid,
              displayName: userData.displayName,
              photoURL: userData.photoURL,
              email: userData.email,
            };
            this.$store.commit("setUser", userDetails);
            this.$store.commit("setLoggedIn", true);
            this.$store.commit("setLoader", { isLoading: false });
          });
        this.$store.dispatch("getSpeakers", {
          userId: user.uid,
        });
      } else {
        console.log("logged out");
        this.$store.commit("setUser", {});
        this.$store.commit("setLoggedIn", false);
        this.$store.commit("setLoader", { isLoading: false });
      }
    });
    this.$store.commit("setLoader", { isLoading: false });
  },
});
</script>