import firebase from "firebase";
import router from "@/router";

export default {
  state: {
    isLoggedIn: false,
    currentUser: {},
  },
  mutations: {
    setUser(state: any, payload: any) {
      state.currentUser = payload;
      localStorage.setItem("currentUser", payload);
      localStorage.setItem("userId", payload.uid);
    },
    setLoggedIn(state: any, payload: boolean) {
      state.isLoggedIn = payload;
    },
  },
  actions: {
    login({ commit }: any, payload: any) {
      commit("setLoader", { isLoading: true });
      firebase
        .auth()
        .signInWithEmailAndPassword(payload.email, payload.password)
        .then(async (user) => {
          const userRef = await firebase
            .firestore()
            .collection("users")
            .doc(user.user?.uid)
            .get();
          const userData = userRef.data();
          const userDetails = {
            uid: user.user?.uid,
            displayName: userData?.displayName,
            photoURL: userData?.photoURL,
            email: userData?.email,
          };
          commit("setUser", userDetails);
          commit("setLoggedIn", true);
          router.push("/dashboard");
          commit("setLoader", { isLoading: false });
        })
        .catch((error) => {
          commit("setSnackbar", {
            showing: true,
            text: error.message,
            color: "error",
          });
          commit("setLoader", { isLoading: false });
        });
    },

    logout({ commit }: any) {
      commit("setLoader", { isLoading: true });
      firebase.auth().signOut();
      commit("setUser", {});
      commit("setLoggedIn", false);
      localStorage.setItem("currentUser", "");
      localStorage.setItem("userId", "");
      router.push("/");
      commit("setLoader", { isLoading: false });
    },
  },
};
